<template>
  <div class="goods">
    <div class="fixed-header">
      <div class="fixed-header-cont">
        <div class="default-header has-line">
          <div class="default-header-cont">
            <div class="left">
              <van-field
                readonly
                clickable
                :value="value"
                placeholder="请选择选择门店"
                @click="showPicker = true"
              />
              <van-popup v-model="showPicker" position="bottom">
                <van-picker
                  show-toolbar
                  :columns="storeList"
                  item-height="36"
                  visible-item-count="6"
                  :default-index="selectedIndex"
                  @cancel="showPicker = false"
                  @confirm="storeChange"
                />
              </van-popup>
            </div>
            <div class="right">
              <div class="link" @click="logout">
                <img class="icon" src="@/assets/img/logout.png" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <Swipe />

    <van-tabs v-model="active" animated>
      <van-tab title="商品查询">
        <div class="menu-list">
          <router-link :to="{name: 'Top10'}" class="link">
            <div class="icon">
              <img src="@/assets/img/sku.png" />
            </div>
            <p>top10</p>
          </router-link>
          <router-link :to="{name: 'GoodsInquiry'}" class="link">
            <div class="icon">
              <img src="@/assets/img/xiaoshou.png" />
            </div>
            <p>出库信息</p>
          </router-link>
          <router-link :to="{name: 'StockInfo'}" class="link">
            <div class="icon">
              <img src="@/assets/img/kucun.png" />
            </div>
            <p>库存信息</p>
          </router-link>
        </div>
      </van-tab>
      <van-tab title="商品追踪">
        <div class="menu-list">
          <router-link :to="{name: 'List'}" class="link">
            <img class="icon" src="@/assets/img/huojialiebiao.png" />
            <p>货架列表</p>
          </router-link>
          <router-link :to="{name: 'List'}" class="link">
            <img class="icon" src="@/assets/img/xinjianhuojia.png" />
            <p>新建货架</p>
          </router-link>
          <router-link :to="{name: 'List'}" class="link">
            <img class="icon" src="@/assets/img/huojiaxiaoneng.png" />
            <p>货架效能</p>
          </router-link>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Vue from "vue";
import { Field, Popup, Picker, Dialog, Tab, Tabs } from "vant";

Vue.use(Field)
  .use(Popup)
  .use(Tab)
  .use(Tabs)
  .use(Dialog)
  .use(Picker);

// @ is an alias to /src
import Swipe from "@/components/Swipe.vue";
import storage from "../../utils/storage";

export default {
  name: "Goods",
  components: {
    Swipe
  },

  data() {
    return {
      value: "",
      showPicker: false,
      selectedIndex: 0,
      storeList: [],
      active: 0
    };
  },
  created() {},
  mounted() {
    this.get_storeList();
    window.addEventListener("scroll", this.handleScroll, true);
  },
  methods: {
    handleScroll(e) {
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      if (scrollTop > 0) {
        storage.set("Home_scrollTop", scrollTop);
      }
    },
    get_storeList() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      let params = {};
      this.serverHost
        .fetchPost("/store/getALlStore29", params)
        .then(res => {
          this.toast.clear();
          const resultData = res.data.data;
          resultData.forEach(element => {
            element.text = element.name;
          });
          this.storeList = resultData;
          // console.log(this.$store.getters.getStoreInfoId);
          if (this.$store.getters.getStoreInfoId) {
            // 判断是否已经储存门店
            resultData.forEach((items, index) => {
              if (items.gid == this.$store.getters.getStoreInfoId) {
                this.selectedIndex = index;
                this.value = items.name;
                this.$store.dispatch("setStoreInfo", items); //储存门店
                this.$store.dispatch("setStoreInfoId", items.gid); //储存门店
              }
            });
          } else {
            this.selectedIndex = 0;
            this.value = resultData[0].name;
            this.$store.dispatch("setStoreInfo", resultData[0]); //储存第一个门店为默认门店
            this.$store.dispatch("setStoreInfoId", resultData[0].gid); //储存第一个门店为默认门店
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    storeChange(picker, value) {
      // console.log(value)
      this.selectedIndex = value;
      this.value = picker.text;
      this.showPicker = false;
      this.$store.dispatch("setStoreInfo", picker); //储存门店
      this.$store.dispatch("setStoreInfoId", picker.gid); //储存门店
    },
    logout() {
      Dialog.confirm({
        title: "提示",
        message: "是否确认退出?"
      })
        .then(() => {
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
    }
  },
  activated() {
    const Home_scrollTop = storage.get("Home_scrollTop");
    // console.log(Home_scrollTop);
    if (Home_scrollTop) {
      window.scrollTo({
        top: Home_scrollTop
      });
    }
  }
};
</script>

